<template>
  <div>
    <img class="user-poster" src="https://img.yzcdn.cn/public_files/2017/10/23/8690bb321356070e0b8c4404d087f8fd.png">
    <van-row class="user-links">
      <van-col span="6">
        <van-icon name="pending-payment" />
        
      </van-col>
      <van-col span="6">
        <van-icon name="pending-orders" />
        
      </van-col>
      <van-col span="6">
        <van-icon name="pending-deliver" />
        
      </van-col>
      <van-col span="6">
        <van-icon name="logistics" />
        
      </van-col>
    </van-row>

    <van-cell-group class="user-group">
      <router-link to="/orders?state=0">
        <van-cell icon="records" title="" is-link />
      </router-link>
      <router-link to="/addresses">
        <van-cell icon="location" title="" is-link />
      </router-link>
    </van-cell-group>

    <van-cell-group>
      <van-cell icon="exchange" title="" is-link />
      <van-cell icon="gold-coin" title="" is-link />
      <van-cell icon="gift" title="" is-link />
    </van-cell-group>
  </div>
</template>

<script>
import { Row, Col, Icon, Cell, CellGroup } from 'vant';

export default {
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
  },
};
</script>

<style lang="less">
.user {
  &-poster {
    width: 100%;
    display: block;
  }

  &-group {
    margin-bottom: 15px;
  }

  &-links {
    padding: 15px 0;
    font-size: 12px;
    text-align: center;
    background-color: #fff;

    .van-icon {
      display: block;
      font-size: 24px;
    }
  }
}
</style>



