<template>
  <div>
    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      :disabled-list="disabledList"
      disabled-text=""
      @add="onAdd"
      @edit="onEdit"
    />
  </div>
</template>

<script>
import {
  AddressList,
  Toast,
} from 'vant';

export default {
  components: {
    [AddressList.name]: AddressList,
  },
  data() {
    return {
      chosenAddressId: '1',
      list: [
        {
          id: '1',
          name: '',
          tel: '13000000000',
          address: ' 138  7  501 ',
        },
        {
          id: '2',
          name: '',
          tel: '1310000000',
          address: ' 50 ',
        },
      ],
      disabledList: [
        {
          id: '3',
          name: '',
          tel: '1320000000',
          address: ' 15 ',
        },
      ],
    };
  },

  methods: {
    onAdd() {
      Toast('');
    },

    onEdit(item, index) {
      Toast(':' + index);
      this.$router.push({ path: `/addresses/${item.id}/edit` });
    },
  },
};
</script>

<style lang="less">

</style>



